export default class ChatNew extends ChatController {
  constructor(props: Props) {
    super(props);
    // Customizable Area Start
    // Customizable Area End
  }

  // Customizable Area Start
  renderItem = (item:UserResponse) => {
    let userPic = item.attributes.profile_pic ?? ''
    let userName = item.attributes.user_name ?? ''
    return(
      <TouchableOpacity
        testID="rowView"
        style={{paddingLeft:normalize(1),
          paddingRight:normalize(1), 
          paddingTop:normalize(5),
          paddingBottom:normalize(5), 
          marginTop:normalize(1), 
          marginBottom:normalize(1), 
        }}
        onPress={() => {
          this.handleRowItemOnPressUser(item);
        }}
      >
        <View style={{flexDirection:"row"}}>
        <View style={styles.profilePhotoView}>
            <View style={styles.profileCameraView}>
              <FastImage
                source={{
                  uri:userPic
                  ? userPic : defaultProfileImage(userName) ,
                }}
                style={styles.profileImageView}
                resizeMode={FastImage.resizeMode.stretch}
              />
            </View>
          </View>

          <View
            style={{
              flex: 1,
              flexDirection: "column",
              justifyContent: "center",
              marginLeft: normalize(5),
            }}
          >
            <Text style={[styles.itemName, { alignSelf: "flex-start" }]} numberOfLines={1}>{userName}</Text>

            </View>
            <Image
            source={
              this.state.currentSelectedUser === item
                ? imgSelectedRadioButton
                : imgDefaultRadioButton
            }
            style={styles.radioButtonImage}
            resizeMode="contain"
          />
        </View>
        </TouchableOpacity>
    );
  }
  // Customizable Area End

  render() {
    // Customizable Area Start
    // Merge Engine - render - Start
    return (
      <ScrollView keyboardShouldPersistTaps="always" style={styles.container}>
        <TouchableWithoutFeedback
          testID={"hideKeyboard"}
          onPress={() => {
            this.hideKeyboard();
          }}
        >
           <View style={globalStyles.viewWindow}>
            {/* header */}
            <View
              style={[
                globalStyles.flexDirectionRow,
                globalStyles.headerRow,
              ]}
            >
              <TouchableOpacity onPress={this.doButtonBackPressed}>
                <Image
                  source={imgArrowBack}
                  style={globalStyles.headerImage}
                  resizeMode="contain"
                />
              </TouchableOpacity>

              {/* header middle */}
              <View style={{flex:1, justifyContent:'center'}}>
                <Text style={globalStyles.headerText}>
                  {configJSON.newMessage}
                </Text>
              </View>

              <TouchableOpacity style={{height:'100%', 
                justifyContent:'center',
                alignSelf:'center'}} onPress={this.doButtonPressChatMessages}>
                <View style={{ 
                justifyContent:'center',
                alignSelf:'center', 
                paddingLeft:normalize(10), 
                paddingRight:normalize(10),
                paddingTop:normalize(3),
                paddingBottom:normalize(3),
                backgroundColor:this.state.currentSelectedUser ? COLORS.redActive : COLORS.grey,
                borderRadius:normalize(10)
                }}>
                <Text style={{color:COLORS.textColor, fontSize:normalize(dimension.defaultText)}}>
                  {configJSON.chat}
                </Text>
                </View>
              </TouchableOpacity>
            </View>

            <View style={styles.searchImageView}>
              <Image
                source={imgSearch}
                style={styles.imageSearch}
                resizeMode="contain"
              />
              <TextInput
                testID="txtInput"
                style={styles.bgMobileInput}
                placeholder={configJSON.searchPlayers}
                {...this.txtInputUserProps}
                onSubmitEditing={this.onPressUserSubmitting}
              />
            </View>

            {this.state.message.length > 0 && (
              <Text style={[globalStyles.apiMessage]}>{this.state.message}</Text>
            )}
            <FlatList
              testID="flatList"
              style={styles.flatList}
              contentContainerStyle={styles.flexGrow}
              data={this.state.userList}
              renderItem={({ item }) => this.renderItem(item)}
            />
         </View>
        </TouchableWithoutFeedback>
      </ScrollView>
    );
    // Merge Engine - render - End
    // Customizable Area End
  }
}

